.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.time-control {
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  input {
    width: 100%;
    //padding: 1rem 0rem;
    // changing appearance of range input
    -webkit-appearance: none;
    background: transparent;
    cursor: pointer;
  }

  p {
    padding: 1rem;
  }
}

.play-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  svg {
    cursor: pointer;
  }
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
  //border-radius: 50%;
  //z-index: 999999;
  //background-color: black;
  //position: sticky;
}

input[type="range"]::-moz-range-thumb {
  -webkit-appearance: none;
  border-radius: 50%;
  background: transparent;
}

.track {
  background: rgb(215, 212, 255);
  width: 100%;
  height: 1rem;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}

.animate-track {
  width: 100%;
  height: 100%;
  background: rgba(218, 218, 218, 0.65);
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 1rem;
  transform: translateX(0%);
  pointer-events: none;
}

@media screen and (max-width: 768px) {
  .time-control {
    width: 90%;
  }

  .play-control {
    width: 60%;
  }
}
