nav {
  min-height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  button {
    background: transparent;
    border: none;
    cursor: pointer;
    border: 2px solid rgb(65, 65, 65);
    border-radius: 0.25rem;
    padding: 0.5rem;
    transition: all 0.3 ease;
    // &:hover {
    //   background: rgb(65, 65, 65);
    //   color: white;
    // }
  }
}

@media screen and (max-width: 768px) {
  nav {
    button {
      z-index: 10;
    }
  }
}
